    top: -27px;
    left: -16px;
    border-top: 2px solid #ffdf01;
    border-left: 2px solid #ffdf01; }
  .title::after {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: -27px;
    right: -16px;
    border-top: 2px solid #ffdf01;
    border-right: 2px solid #ffdf01; }

