  position: relative;
  display: inline-block;
  font-family: Catamaran;
  font-size: 56px;
  font-weight: 700;
  text-align: center;
  margin: 0; }
  .title::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: -27px;
    left: -16px;
    border-top: 2px solid #ffdf01;
    border-left: 2px solid #ffdf01; }
  .title::after {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: -27px;
    right: -16px;
    border-top: 2px solid #ffdf01;
    border-right: 2px solid #ffdf01; }

/*Описание на страницах*/
.description {
  font-family: "PT Serif";
  font-size: 16px;
  font-weight: 400;
  line-height: 26px; }

/*Кнопка*/
.button {
  display: inline-block;
  color: white;
  font-family: Catamaran;
  font-size: 11px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  padding: 10px 21px;
  border: 1px solid #fff;
  text-decoration: none;
  margin: 0px auto;
  position: relative; }
  .button::before {
    content: "";
    height: 1px;
    width: 30px;
    position: absolute;
    top: 23px;
    left: -21px;
    background-color: #ffdf01; }
  .button::after {
    content: "";
    height: 1px;
    width: 30px;
    position: absolute;
    top: 23px;
    right: -21px;
    background-color: #ffdf01; }

/*Иконки социальных сетей*/
.social {
  padding: 0px;
  margin: 0px;
  transition: 2.5s; }
  .social--team {
    display: none;
    margin-bottom: 45px; }
  .social__item {
    width: 36px;
    height: 36px;
    margin: 10px;
    display: inline-block;
    border: 1px solid #fff;
    color: #fff;
    transition: .5s; }
    .social__item--footer {
      border: 1px solid #383838; }
    .social__item:hover {
      background-color: #ffdf01;
      border: 1px solid transparent;
      color: #000; }
  .social__link {
    display: block;
    width: 100%;
    height: 100%;
    color: #fff;
    text-align: center;
    line-height: 2; }
    .social__link--footer {
      color: #383838; }

/* Заголовок в шапках на страницах*/
.main-title-page {
  color: white;
  font-family: Catamaran;
  font-size: 80px;
  font-weight: 900;
  line-height: 25px;
  text-transform: uppercase;
  position: relative; }
  .main-title-page::before {
    content: "";
    display: block;
    width: 136px;
    height: 21px;
    position: absolute;
    border: 2px solid #ffdf01;
    border-bottom: 1px solid transparent;
    top: -50px;
    left: 50%;
    transform: translateX(-50%); }
  .main-title-page::after {
    content: "";
    display: block;
    width: 136px;
    height: 21px;
    position: absolute;
    border: 2px solid #ffdf01;
    border-top: 1px solid transparent;
    top: 43px;
    left: 50%;
    transform: translateX(-50%); }

/* Заголовок секций на странице*/
.pages-title {
  color: #333;
  font-family: Catamaran;
  font-size: 56px;
  font-weight: 900;
  text-transform: uppercase;
  position: relative;
  padding: 0px 10px;
  display: inline-block;
  line-height: 1; }
  .pages-title::before {
    content: "";
    position: absolute;
    display: inline-block;
    width: 100%;
    height: 15px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -75%);
    background-color: #ffdf01;
    z-index: -1; }

/* Описание секций на странице*/
.pages-description {
  color: #555;
  font-family: PTSerif;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px; }

.button__blog {
  border: 1px solid #ffdf01;
  color: #333;
  padding: 10px 100px; }
  .button__blog::before {
    background-color: #222; }
  .button__blog::after {
    background-color: #222; }

.blog-wrapper {
  padding: 100px 0px 160px 0px; }

.blog {
  width: 100%;
  display: none; }
  .blog__img {
    width: 100%;
    background-color: red;
    max-height: 400px;
    display: block;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    overflow: hidden; }
    .blog__img::after {
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.5);
      position: absolute; }
    .blog__img img {
      width: 100%; }
  .blog__info {
    position: absolute;
    display: flex;
    bottom: 30px;
    left: 30px;
    z-index: 2; }
  .blog__date {
    width: 50px;
    height: 46px;
    background-color: #ffdf01;
    text-align: center;
    padding: 10px 0px; }
  .blog__day {
    color: #222;
    font-family: Catamaran;
    line-height: 1;
    font-size: 20px;
    font-weight: 700;
    margin: 0px;
    padding: 0px; }
  .blog__moans {
    color: #222;
    font-family: Catamaran;
    line-height: 5px;
    font-size: 10px;
    font-weight: 600;
    text-transform: uppercase;
    margin: 0px;
    padding: 0px; }
  .blog__auter {
    margin-right: 30px;
    margin-left: 30px;
    font-family: Catamaran;
    font-weight: 600;
    line-height: 14px;
    text-transform: uppercase;
    color: #fdfdfd;
    font-size: 10px;
    line-height: 26px; }
  .blog__like {
    margin-right: 30px;
    margin-left: 30px;
    font-family: Catamaran;
    font-weight: 600;
    line-height: 14px;
    text-transform: uppercase;
    color: #fdfdfd;
    font-size: 10px;
    line-height: 26px; }
    .blog__like span {
      color: #ffdf01;
      font-size: 12px; }
    .blog__like i {
      margin: 0px 5px;
      vertical-align: middle; }
  .blog__comment {
    margin-right: 30px;
    margin-left: 30px;
    font-family: Catamaran;
    font-weight: 600;
    line-height: 14px;
    text-transform: uppercase;
    color: #fdfdfd;
    font-size: 10px;
    line-height: 26px; }
    .blog__comment span {
      color: #ffdf01;
      font-size: 12px; }
    .blog__comment i {
      margin: 0px 5px;
      vertical-align: middle; }
  .blog__title {
    color: #333;
    font-family: Catamaran;
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    margin: 25px 0px;
    display: block; }
  .blog__description {
    color: #919191;
    font-family: "PT Serif";
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 50px; }

